.center{
  display: flex;
  justify-content: center;
  height: 100vh;
  width: 100vw;
}
.paragraph{
  max-width: 1400px;
  margin: auto;
  min-width: 300px;
  height: 412px;
  overflow: hidden;
  margin: auto;
  color: #f5f5f5;
  font-size: 25px;
  font-weight: 400;
  font-family: 'Roboto Mono', monospace;
}
html{
  background-color: rgb(0,104,198);
}
.letter{
  display: inline;
}
.current{
  border-left: 5px solid rgb(58,191,102);
}
@keyframes blinker {
  50% {
    border-color: rgb(0,104,198);
  }
}
.blink {
  animation: blinker 1s step-start infinite;
}
.hidden{
  display: none;
}
.correct{
  color: rgb(77,219,71);
}
.wrong{
  color: red;
}
.result{
  width: 1400px;
  height: 412px;
  overflow: hidden;
  margin: auto;
  color: #f5f5f5;
  font-size: 50px;
  font-weight: 400;
  font-family: 'Roboto Mono', monospace;
}
.hide{
visibility: hidden;
}
@media (min-width:600px){
  .paragraph{
   
    width: calc(100vw - 200px);
    font-size: 50px;
  }
}
.difficulty{
  width: 700px;
  margin: auto;
  text-align: center;
  color: white;
}
.button{
  margin: auto;
  border: 1px solid white;
  width: 200px;
  text-align: center;
  padding-top: 30px;
  padding-bottom: 30px;
  background-color: black;
  color: white;
  border-radius: 50px;
  margin-top: 20px;
}
.button:hover{
  cursor: pointer;
  color: green;
}